
import React, { Component } from 'react';
import { InputGroup, FormControl, Button } from 'react-bootstrap';
import { Row, Col} from "react-grid-system";
import Axios from 'axios';
import Contacto from '../Contacto/Contacto';
import {connect} from 'react-redux'
class Actualizar extends Component {

    state = {
        id:'',
        first_name: '',
        last_name:'',
        email: '',
        age: '',
        phone:'',
        biography:'',
        err:'',
        redirect: false
    } 

    componentDidUpdate(prevProps){
        if(prevProps.actualizar.first_name !== this.props.actualizar.first_name || prevProps.actualizar.last_name !== this.props.actualizar.last_name || prevProps.actualizar.email !== this.props.actualizar.email || prevProps.actualizar.age !== this.props.actualizar.age || prevProps.actualizar.phone !== this.props.actualizar.phone || prevProps.actualizar.biography !== this.props.actualizar.biography){
          this.setState({id: this.props.actualizar._id, first_name: this.props.actualizar.first_name, last_name: this.props.actualizar.last_name, email: this.props.actualizar.email, age: this.props.actualizar.age, phone: this.props.actualizar.phone, biography: this.props.actualizar.biography, redirect: false})
        }
    }
  
    componentDidMount(){
        this.setState({id: this.props.actualizar._id, first_name: this.props.actualizar.first_name, last_name: this.props.actualizar.last_name, email: this.props.actualizar.email, age: this.props.actualizar.age, phone: this.props.actualizar.phone, biography: this.props.actualizar.biography, redirect: false})
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    redirigir = () => {
        this.setState({
            redirect:false
        })
    }
    handleSubmit = async (e) => {
        // e.preventDefault();
        const {first_name, last_name, email, age, phone, biography, id} = this.state;

        const data = {
            first_name: first_name, 
            email: email, 
            last_name: last_name,  
            age: age, 
            phone: phone, 
            biography: biography
        }
        if(this.checkForm(data) !== false){
        Axios
        .post(`${process.env.REACT_APP_LAB2_WAN}/agenda/${id}`, data)
            .then(response => {
                this.props.saveRedirect({redirect:true})
                this.props.changeRedirect(true)
            })
        }
    }
    changeRedirect=()=>{
        this.setState({redirect:false})
    }
    checkForm = (form) => {
        var re;
        re = /[0-9]/;
        if(re.test(form.first_name)) {
        this.setState({err:"Error: the first name cannot contain numbers."});
        return false;
        }
        if(re.test(form.last_name)) {
            this.setState({err:"Error: the last name cannot contain numbers."});
            return false;
        }
        if(form.first_name === "") {
        this.setState({err:"Error: the first name cannot be blank."});
        return false;
        }
        if(form.last_name === "") {
        this.setState({err:"Error: the last name cannot be blank."});
        return false;
        }
        if(form.email === "") {
            this.setState({err:"Error: the email name cannot be blank."});
            return false;
        }
        if(form.age === "") {
            this.setState({err:"Error: the age cannot be blank."});
            return false;
        }
        if(form.biography === "") {
            this.setState({err:"Error: the biography cannot be blank."});
            return false;
        }
        if(form.phone === "") {
            this.setState({err:"Error: the phone cannot be blank."});
            return false;
        }
        // eslint-disable-next-line
        re = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,4})+$/;
        if(!re.test(form.email)) {
        this.setState({err:"Error: the email address is incorrect."});
        return false;
        }
        // eslint-disable-next-line
        re = /[\(]?[\+]?(\d{2}|\d{3})[\)]?[\s]?((\d{6}|\d{8})|(\d{3}[\*\.\-\s]){3}|(\d{2}[\*\.\-\s]){4}|(\d{4}[\*\.\-\s]){2})|\d{8}|\d{10}|\d{12}/
        if(!re.test(form.phone)) {
        this.setState({err:"Error: the phone number is incorrect."});
        return false;
        }
        if(form.age === 0 || form.age > 110 || form.age < 0) {
            this.setState({err:"Error: The age must be between 1 and 110."});
            return false;
        }
        return true;
    }

  render() {
    let {first_name, last_name, email, age, phone, biography, id} = this.state;
    let contacto= {first_name, last_name, email, age, phone, biography, _id:id}

    if (this.props.redirectRedux.redirect === true){
       return <Contacto contacto={contacto} actualizarContact={this.props.actualizarContact} changeRedirect={this.props.changeRedirect}/>
    }
    return (
      <Row className="box_styles" style={{marginRight:"0px !important", marginLeft:"0px !important"}}>
          <Col md={12} className="superCenter">
              <p style={{fontWeight:"bold"}}>
                  Actualizar Contacto
              </p>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"23px"}} class="fas fa-user-alt"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="First Name"
                  aria-label="First Name"
                  aria-describedby="basic-addon1"
                  id="first_name"
                  value={this.state.first_name}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"20px"}} class="fas fa-money-check"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="Last Name"
                  aria-label="Last Name"
                  aria-describedby="basic-addon1"
                  id="last_name"
                  value={this.state.last_name}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"23px"}} class="fas fa-phone"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="Ej: (55)4125555555"
                  aria-label="Phone"
                  aria-describedby="basic-addon1"
                  id="phone"
                  value={this.state.phone}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"23px"}} class="fas fa-at"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="Email"
                  aria-label="Email"
                  aria-describedby="basic-addon1"
                  id="email"
                  value={this.state.email}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"23px"}} class="fas fa-clock"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="Age"
                  aria-label="Age"
                  aria-describedby="basic-addon1"
                  id="age"
                  value={this.state.age}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12}>
              <InputGroup className="mb-3">
              <InputGroup.Prepend>
                  <InputGroup.Text id="basic-addon1"><i style={{fontSize:"24px"}} class="fas fa-book"></i></InputGroup.Text>
              </InputGroup.Prepend>
              <FormControl
                  placeholder="Biography"
                  aria-label="Biography"
                  aria-describedby="basic-addon1"
                  id="biography"
                  value={this.state.biography}
                  onChange={(e) => this.handleChange(e)}
              />
              </InputGroup>
          </Col>
          <Col md={12} className="superCenter">
              <Button className="side_button" onClick={() => this.handleSubmit()}>Actualizar</Button>
          </Col>
          {
            this.state.err &&
            <Col md={12} className="mt-3">
              <div class="alert alert-danger" role="alert">
                  {this.state.err}
              </div>
            </Col>
          }
      </Row>
    )
  }
}

const mapStateToProps = state => ({ redirectRedux: state.redirect });
 
const mapDispatchToProps = dispatch => ({
    saveRedirect: (data = {}) => {
      dispatch({
        type: "SAVE_REDIRECT",
        data
      });
    }
  });


  export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Actualizar);