import React, { Component } from 'react';
import './index.css';
import { Row, Col} from 'react-grid-system';
import Crear from './Crear/Crear';
import ConsultarAgenda from './ConsultarAgenda/ConsultarAgenda';
import Axios from 'axios';
import Contacto from './Contacto/Contacto';
import Actualizar from './Actualizar/Actualizar';
import {connect} from 'react-redux';

class Home extends Component {
    state = {
        crear: false,
        consultar_agenda: false,
        contactos:'',
        contacto:false,
        actualizar:false,
        redirect:false
    } 

    actualizarContact = (contact) =>{
        this.props.saveRedirect({redirect:false})
        this.setState({actualizar: contact, crear:false, consultar_agenda:false, contacto:false,redirect:this.state.redirect})
    }

    changeRedirect = (x) => {
        this.setState({
            redirect:x
        })
    }
    componentDidMount(){
        Axios
        .get(`${process.env.REACT_APP_LAB2_WAN}/agenda/all`)
        .then(response => {
            this.setState({contactos: response.data.agendas})
        })
    }

    componentDidUpdate(prevState){
        if(prevState.redirect !== this.state.redirect){
            Axios
            .get(`${process.env.REACT_APP_LAB2_WAN}/agenda/all`)
            .then(response => {
                this.setState({contactos: response.data.agendas})
            })
        }
    }
    render() { 
        return ( 
            <div className="background_styles">
                <Row style={{marginRight:"0px !important", marginLeft:"0px !important"}}>
                    <Col md={3}>
                        <div class="btn-group-vertical" role="group" aria-label="Button group with nested dropdown" style={{paddingTop:"50%", paddingLeft:"10%"}}>
                            <button type="button" class="btn menu_styles" onClick={() => this.setState({crear:true, consultar_agenda:false, contacto:false, actualizar:false})}>Crear contacto</button>
                            <button type="button" class="btn menu_styles" onClick={() => this.setState({consultar_agenda:true, crear:false, contacto:false, actualizar:false})}>Consultar agenda</button>

                            <div class="btn-group" role="group">
                                <button id="btnGroupDrop1" type="button" class="btn menu_styles dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                Consultar contacto
                                </button>
                                <div class="dropdown-menu" aria-labelledby="btnGroupDrop1">
                                    {
                                        this.state.contactos &&
                                        this.state.contactos.map((item, key)=>(
                                            <button type="button" key={key} style={{textTransform:"capitalize"}} class="btn" onClick={() => this.setState({consultar_agenda:false, crear:false, contacto: item, actualizar:false})}>{item.first_name} {item.last_name}</button>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>
                    </Col>
                    <Col md={9} style={{padding:"0px 0px"}}>
                        {
                            this.state.crear &&
                            <Crear/>
                        }
                        {
                            this.state.consultar_agenda &&
                            <ConsultarAgenda/>
                        }
                        {
                            this.state.contacto &&
                            <Contacto
                                contacto={this.state.contacto}
                                actualizarContact={this.actualizarContact}
                                changeRedirect={this.changeRedirect}
                            />
                        }
                         {
                            this.state.actualizar &&
                            <Actualizar
                                actualizar={this.state.actualizar}
                                actualizarContact={this.actualizarContact}
                                redirect={this.state.redirect}
                                changeRedirect={this.changeRedirect}
                            />
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}
 
const mapStateToProps = state => ({ redirectRedux: state.redirect });
 
const mapDispatchToProps = dispatch => ({
    saveRedirect: (data = {}) => {
      dispatch({
        type: "SAVE_REDIRECT",
        data
      });
    }
  });

  export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Home);
