import React, { Component} from 'react'
import { Row, Col } from 'react-grid-system';
import { Table } from 'react-bootstrap';
import Axios from 'axios';
import '../index.css';


class ConsultarAgenda extends Component {
    state = { 
        agenda:[]
    }
    componentDidMount(){
        Axios
        .get(`${process.env.REACT_APP_LAB2_WAN}/agenda/all`)
        .then(response => {
            this.setState({agenda: response.data.agendas})
        })
    }

    deleteContact(index){
        Axios
        .delete(`${process.env.REACT_APP_LAB2_WAN}/agenda/${this.state.agenda[index]._id}`)
        .then(response => {
            if( response.data.ok === true){
                this.state.agenda.splice(index, 1);
                this.setState({ agenda: this.state.agenda });
            }
        })
    }

    render() { 
        var dataUser='';
        dataUser =this.state.agenda ? this.state.agenda.map((item, key)=>{
            return(
                <tr>
                    <td>{item.first_name}</td>
                    <td>{item.last_name}</td>
                    <td>{item.email}</td>
                    <td>{item.phone}</td>
                    <td>{item.age}</td>
                    <td className="text-justify">
                        {item.biography}
                    </td>
                    <td>
                        <button className="btn" onClick={() => this.deleteContact(key)}><i class="fas fa-trash"></i></button>
                    </td>
                </tr>
            )
        } ):null
        return ( 
            <>
                <Row style={{marginRight:"0px !important", marginLeft:"0px !important"}}>
                    <Col md={12} style={{textAlign:'left', padding:'2% 30% 2% 2%'}}>
                        <Table hover striped responsive className="box_styles">
                            <thead>
                                <tr>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>First Name</th>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>Last Name</th>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>Email</th>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>Phone</th>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>Age</th>
                                    <th style={{color:'#f4a900', textTransform:'uppercase'}}>Biography</th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                            {dataUser}
                                {}
                            </tbody>
                        </Table>
                    </Col>
                </Row>
            </>
         );
    }
}
 
export default ConsultarAgenda;
