
import React, { Component } from 'react';
import { Row, Col} from "react-grid-system";
import {connect} from 'react-redux'

class Contacto extends Component {
  state = {
    contacto:''
  } 

componentDidMount(){
  this.setState({contacto: this.props.contacto})
}

componentDidUpdate(prevProps){
  if(prevProps.contacto.first_name !== this.props.contacto.first_name || prevProps.contacto.last_name !== this.props.contacto.last_name || prevProps.contacto.email !== this.props.contacto.email || prevProps.contacto.phone !== this.props.contacto.phone || prevProps.contacto.age !== this.props.contacto.age || prevProps.contacto.biography !== this.props.contacto.biography){
    this.setState({contacto: this.props.contacto})
  }
}

  render() {
      let {contacto} = this.state;
    return (
      <Row className="box_styles" style={{marginRight:"0px !important", marginLeft:"0px !important"}}>
          <Col md={11} className="superCenter">
              <p style={{textTransform:"capitalize", fontWeight:"bold"}}>
                 {contacto.first_name} {contacto.last_name}
              </p>
          </Col>
          <Col md={1}>
            <button className="btn" onClick={() => this.props.actualizarContact(contacto)}><i class="far fa-edit"></i></button>
          </Col>
          <Col md={6}>
              <p>
                  <span style={{color:"#f4a900", fontWeight:"bold"}}>EMAIL:</span><br/>
                    {contacto.email}
              </p>
          </Col>
          <Col md={6}>
                <p>
                <span style={{color:"#f4a900", fontWeight:"bold"}}>PHONE:</span><br/>
                    {contacto.phone}
                </p>
          </Col>
          <Col md={6}>
                <p>
                <span style={{color:"#f4a900", fontWeight:"bold"}}>AGE:</span><br/>
                    {contacto.age}
                </p>
          </Col>
          <Col md={6}>
                <p>
                <span style={{color:"#f4a900", fontWeight:"bold"}}>BIOGRAPHY:</span><br/>
                  <p className="text-justify">{contacto.biography}</p>
                </p>
          </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({ redirectRedux: state.redirect });
 
const mapDispatchToProps = dispatch => ({
    saveRedirect: (data = {}) => {
      dispatch({
        type: "SAVE_REDIRECT",
        data
      });
    }
  });


  export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(Contacto);
